.messageSender {
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 5px 7px -7px rgba(0, 0, 0, 0.75);
  width: 100%;
}

.messageSender__top {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding: 15px;
}

.messageSender__top > form {
  flex: 1;
  display: flex;
}

.messageSender__top > form > input {
  outline-width: 0;
  border: none;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #eff2f5;
}

.messageSender__top > form > button {
  display: none;
}

.messageSender__input {
  flex: 1;
}

.messageSender__bottom {
  display: flex;
  justify-content: space-evenly;
}

.messageSender__option {
  padding: 20px;
  display: flex;
  align-items: center;
  color: grey;
  cursor: pointer;
  margin: 5px;
}

.messageSender__option > h3 {
  font-size: medium;
  margin-left: 10px;
}

.messageSender__option:hover {
  background-color: #eff2f5;
  border-radius: 20px;
}

/* Media */
@media screen and (max-width: 425px) {
  .messageSender__top {
    padding: 30px;
  }

  .messageSender__top > .MuiAvatar-root {
    width: 60px;
    height: 60px;
  }

  .messageSender__top > form > input {
    font-size: 22px;
    padding: 10px 20px;
  }

  .messageSender__option > .MuiSvgIcon-root {
    width: 30px;
  }

  .messageSender__option > h3 {
    font-size: 22px;
    margin-left: 10px;
  }
}
